const Posts = ({users,deleteCallback,editCallback,updateCallback,editValue}) => {
    const handleLike = (e) => {
        const newClass = e.target.className === 'post-detail__icon far fa-heart' ? 'post-detail__icon far fa-heart like' : 'post-detail__icon far fa-heart';
        e.target.className = newClass;
    }
    const list = users.map((user, index) => {
        if(user.option === 'edit'){
            return (
            <div className='post'>
            <textarea className='post-edit-input' ref={editValue}>{user.comment}</textarea>
            <div className="post-edit-btn">
            <button className='btn btn-primary' id={'save'+index} onClick={updateCallback}>Save</button>
            <button className='btn btn-dark' id={'edit'+index} onClick={editCallback}>Cancel</button>
            </div>
            </div>)
        }
        return (
            <div className="post">
                <div className="post-user">
                    <div className="post-user_img">
                    <img src={user.image} alt={user.firstName}/>
                    </div>
                    <h5>{user.firstName} {user.lastName}</h5>
                </div>
                <div className="post-comment">
                    <p>{user.comment}</p>
                </div>
                <div className="post-detail">
                    <div className="post-detail-tools">
                        <i className="post-detail__icon far fa-edit" id={'edit'+index} onClick={editCallback}></i>
                        <i className="post-detail__icon far fa-trash-alt" id={'del'+index} onClick={deleteCallback}></i>
                    </div>
                    <div className="post-detail-reaction">
                        <i className="post-detail__icon far fa-comment"></i>
                        <i className="post-detail__icon far fa-heart" onClick={handleLike}></i>
                        <i className="post-detail__icon fas fa-retweet"></i>
                    </div>
                    <i className='time'>{user.time}</i>
                </div>
            </div>)
    });
    return list;
}
const App = () => {
    const [posts,setPosts] = useState(users);
    const [stas,setStas] = useState(true);
    const ref = useRef(null);
    const editValue = useRef(null);
    const handleClick = () => {
        let value = ref.current.value;
        const newUser = {
            id: 3,
            image:user_img2,
            firstName: 'Anonymous',
            lastName: ':>',
            comment : value,
            time:getTime(),
            option:'get',
        }
        users.push(newUser);
        const newList = [...users];
        ref.current.value = '';
        setStas(true);
        ref.current.blur();
        setPosts(newList);
    }
    const handleDelete = (e) => {
        // console.log(typeof e.target.id);
        if(window.confirm('Are you sure you want to delete ??')){
            const pos = e.target.id;
            const index = parseInt(pos.substring(pos.length - 1,pos.length));
            users.splice(index, 1);
            const newList = [...users];
            setPosts(newList);
        }
    }
    const handleEdit = (e) => {
        const pos = e.target.id;
        const index = parseInt(pos.substring(pos.length - 1,pos.length));
        const currentOption = users[index].option;
        const newOption = currentOption === 'get' ? 'edit' : 'get';
        users[index].option = newOption;
        const newList = [...users];
        setPosts(newList);
    }
    const handleUpdate = (e) => {
        const pos = e.target.id;
        const index = parseInt(pos.substring(pos.length - 1,pos.length));
        users[index].comment = editValue.current.value;
        users[index].option = 'get';
        const newList = [...users];
        setPosts(newList);
    }
    const handleChange = () => {
        if(ref.current.value !== '') {
            setStas(false);
        }
        else{
            setStas(true);
        }
    }
    return (
        <div className="App">
            <TextField className="post post-input" name="add-post" placeholder='Write something...' elemAfterInput={<button className="btn btn-primary" onClick={handleClick} disabled={stas}>Add Post</button>} ref={ref} onChange={handleChange}></TextField>
            <Posts users={posts} deleteCallback={handleDelete} editCallback={handleEdit} updateCallback={handleUpdate} editValue={editValue}/>
        </div>
    )
}
export default App;